<template>
  <div id="content">
    <Header title="主体智能查验系统"></Header>
    <div class="hot_swtich">
      <router-link class="hot_item active" tag="div" to="/check/enterprise">查验企业</router-link>
      <router-link class="hot_item" tag="div" to="/check/individual">查验个人</router-link>
      <router-link class="hot_item" tag="div" to="/checkList">查验记录</router-link>
    </div>
<!--    <div class="exit">-->
<!--      <input style="right: 0" type="button" value="重置规则" @click="exit"></input>-->
<!--    </div>-->
    <!--        二级路由渲染-->
    <div>
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </div>
    <!--        <Footer></Footer>-->
  </div>
</template>
<script>
import Header from '../../components/Header'
import Footer from '../../components/Footer'

export default {
  name: 'check',
  components: {
    Header,
    Footer
  },
  methods: {
    exit: function (){
      this.$router.push({ path: '/rulesCreated'}).catch(error => {})
    }
  },
}
</script>

<style scoped>
#content {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.exit {
  margin: 20px 20px auto auto;
}

.exit input {
  margin-right: 20px;
}

/*.divline{*/
/*    width: 85%;*/
/*    height: 45px;*/
/*    border-bottom: 1px solid #e6e6e6;*/
/*    display: flex;*/
/*    justify-content: space-between;*/
/*    margin-left: auto;*/
/*    margin-right: auto;*/
/*}*/
.divline {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  border-bottom: 2px solid #e6e6e6;
}

.hot_swtich {
  display: flex;
  height: 100%;
  line-height: 45px;
  margin-left: 38%;
  margin-right: 35%;
  margin-bottom: 10px;
}

.hot_item {
  font-size: 20px;
  color: #0c4469;
  width: 100px;
  text-align: center;
  margin: 0 12px;
  font-weight: 700;
  cursor: pointer;
}

.hot_item.router-link-active {
  color: red;
  border-bottom: 2px solid red;
}
</style>



